.sessiondescuento .bloquedescuento .llamadoaccion {
  position: absolute;
  background: rgba(255, 255, 255, 0.7);
  padding: 0 2rem;
  width: 81%;
  text-align: center;
  border-radius: 0.4rem;
  transition: 300ms;
  text-transform: uppercase;
  font-weight: 700;
  animation-name: mianimacion;
  animation-duration: 4s;
  animation-delay: 2s;
  animation-iteration-count: infinite;
  cursor: pointer;
  left: 50%;
  transform: translateX(-50%);
  bottom: -129px;
}
@keyframes mianimacion {
  0% {
    background: rgba(255, 255, 255, 0.7);
  }
  25% {
    background: rgba(13, 166, 243, 0.7);
  }
  50% {
    background: rgba(255, 255, 255, 0.7);
  }
  75% {
    background: rgba(13, 166, 243, 0.7);
  }
  100% {
    background: rgba(255, 255, 255, 0.7);
  }
}
